.du__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 5rem;
}

.du__navbar-links { 
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.du__navbar-links_logo {
    margin-right: 2rem;
    
}

.du__navbar-links_logo img {
    width: 62.56px ;
    height: 16.02px; 
}

.du__navbar-links_container {
    display: flex;
    flex-direction: row;
}

.du__navbar-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.du__navbar-links_container p,
.du__navbar-sign p,
.du__navbar-menu_container p {
    color : #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 25px;
    text-transform: capitalize;

    margin: 0 1rem;
    cursor: pointer;
}

.du__navbar-sign button,
.du__navbar-menu_container button {
    padding: .5rem 1rem;
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    left: 1153px;
    top: 55px;
    background: #FF4820;
    border-radius: 5px;
    font-size: 18px;
    line-height: 25px;
    border: 0;
    outline: none;
    cursor: pointer;
}

.du__navbar-menu {
    margin-left: 1rem;

    display: none;
    position: relative;
}

.du__navbar-menu svg {
    cursor: pointer;
}

.du__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;

    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right:0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0,0,0,0.2);
}

.du__navbar-menu_container p {
    margin: 1rem 0;
}

.du__navbar-menu_container-links-sign {
    display: none; 
}

@media screen and (max-width:1000px)  {
    .du__navbar-links_container {
        display: none;
    }
    .du__navbar-menu {
        display: flex; 
    }

}

@media screen and (max-width:700px)  {
    .du__navbar {
        padding: 2rem 4rem;
    }
}

@media screen and (max-width:550px)  {
    .du__navbar {
        padding: 2rem;
    }
    .du__navbar-sign {
        display: none; 
    }
    .du__navbar-menu_container {
        top: 20px;
    }
    .du__navbar-menu_container-links-sign {
        display: block; 
    }
}




